.calendar {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.calendar__field {
  flex-wrap: wrap;
  gap: 1px;
  width: 710px;
  margin: auto;
  display: flex;
}

.calendar__day {
  box-sizing: border-box;
  content: "";
  cursor: pointer;
  background-color: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
  padding: 0;
  transition: transform .5s;
  display: inline-flex;
}

.calendar__day:before {
  content: attr(data-day);
}

.calendar__day:hover {
  background-color: pink;
  transform: translateY(-20px);
}

